.menuOption {
  background-color: #242424;
  width: 350px;
  cursor: pointer;
  h2 {
    margin: 0;
    color: #ddd;
  }

  i {
    padding: 10px;
  }

  .row {
    height: 40px;
    display: flex;
    align-items: center;
    color: #ddd;
  }

  .row.padding {
    padding-left: 30px;
  }
  .row.padding:hover {
    background: #424242;
  }
  .active {
    background: #424242;
  }
}
