#multipleMedia {
  background: #000;
  .multipleMedia__container {
    width: 100%;
    height: 100%;
    background: #fff;
    color: #000;
    overflow: auto;
    align-items: center;
    display: flex;
    flex-wrap: wrap;

    .media-element {
      float: left;
      display: flex;
      justify-content: center;
      align-items: center;
      .media-element-container {
        width: calc(100% - 10px);
        height: calc(100% - 10px);
        background: #ff0;
      }
    }
  }
}
