.remote-video-component {
  padding: 10px;
  max-height: 100vh;
  overflow-y: auto;
  .videos-user-component {
    width: 100%;
    height: auto;
    border: 1px solid #3f51b5;
    border-radius: 5px;
    padding: 3px;
    margin-bottom: 15px;
    position: relative;
    min-height: 110px;
    .peerName {
      color: #fff;
      text-shadow: 0 0 black;
      font-weight: bold;
      position: absolute;
      max-width: 90%;
      bottom: 2px;
      background-color: #3f51b5;
      padding: 2px 6px;
      border-radius: 5px;
      transform: translateX(-50%);
      left: 50%;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
    video {
      border-radius: 5px;
    }
  }
}

.local-video-component {
  max-height: 100vh;
  overflow-y: auto;
  .local-video-item {
    position: relative;
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 5px;
    video{
      border-radius: 5px;
      border: 1px solid #666;
    }
   
  }
}
